import { 
          RESTART_USERTYPES_RESPONSE,
          USERTYPES_LOADING, 
          USERLIST_SUCCESS, 
          USERLIST_ERROR, 
          USERTYPES_CODE_ERROR, 
          USERTYPES_ERROR,  
          USERTYPES_SUCCESS, 
          RESTART_USERLIST_RESPONSE,
          USERLIST_LOADING,
          USERLIST_CODE_ERROR,
          RESTART_USER_ACTIVATE_RESPONSE,
          USER_ACTIVATE_LOADING,
          USER_ACTIVATE_SUCCESS,
          USER_ACTIVATE_ERROR,
          RESTART_USER_ENABLE_RESPONSE,
          USER_ENABLE_LOADING,
          USER_ENABLE_SUCCESS,
          USER_ENABLE_ERROR,
          USER_ENABLE_CODE_ERROR,
          RESTART_SINGLE_USER_PROFILE,
          SINGLE_USER_PROFILE_LOADING,
          SINGLE_USER_PROFILE_SUCCESS,
          SINGLE_USER_PROFILE_ERROR,
          SINGLE_USER_PROFILE_CODE_ERROR
        } from "../ActionTypes/UserProfileTypes";
import { 
    UserTypeListService, 
    UserListService, 
    ActivateOrDeactivateUserAccountService, 
    EnableOrDisableUserAccountService,
    getSingleUserProfileDetailService
        } from "../../services/UserProfileService";


export const LoadUserTypesAction = () =>
{
   return (dispatch)=>{
 
       dispatch({type:RESTART_USERTYPES_RESPONSE});
       dispatch({type:USERTYPES_LOADING});

    UserTypeListService().then((res)=>{

               console.log(res);

               if(res.hasOwnProperty('success') && res.success==true){
                dispatch({type:USERTYPES_SUCCESS ,res});
              

               }else if(res.hasOwnProperty('success') && res.success==false) {
                    dispatch({type:USERTYPES_ERROR,res});  
               }
           },
           error=>{
            dispatch({type:USERTYPES_CODE_ERROR,error});
               console.log(error);
           }
       )
   } 

}

export const clearUserTypesState= ()=>
{
   return (dispatch)=>
   {
       dispatch({type:RESTART_USERTYPES_RESPONSE});
   }
}



export const ViewUserListAction = (perPage,pageIndex,input,options) =>
{
   return (dispatch)=>{
 
       dispatch({type:RESTART_USERLIST_RESPONSE});
       dispatch({type:USERLIST_LOADING});

    UserListService(perPage,pageIndex,input,options).then((res)=>{

               console.log(res);

               if(res.hasOwnProperty('success') && res.success==true){
                dispatch({type:USERLIST_SUCCESS ,res});
              

               }else if(res.hasOwnProperty('success') && res.success==false) {
                    dispatch({type:USERLIST_ERROR,res});  
               }
           },
           error=>{
            dispatch({type:USERLIST_CODE_ERROR,error});
               console.log(error);
           }
       )
   } 

}

