import HttpService from './HttpService';
import Cookies from 'js-cookie'




export const UserListService = (perPage,pageIndex,input,options) =>
{
    const http = new HttpService();
    
    let addedUrl = "";
    /**
     * if input and options select is empty
     */
    if((input==null || input=="") && (options==null || options==""))
    {
        addedUrl = "admin/user-list/"+perPage+"?page="+pageIndex;
    }
    else if((input!=null || input !="") && (options==null || options=="") )
    {
        addedUrl = "admin/filter-by-input/"+input+"/"+perPage+"?page="+pageIndex;
    }
    else if((input==null || input =="") && (options!=null || options!=""))
    {
        addedUrl = "admin/filter-by-options/"+options+"/"+perPage+"?page="+pageIndex;
    }
    else if((input!=null || input !="") && (options!=null || options!=""))
    {
        addedUrl = "admin/filter-by-input-and-options/"+options+"/"+input+"/"+perPage+"?page="+pageIndex;
    }
    
    let tokenId = 'vt-music';
    return http.getData(addedUrl,tokenId).then((data)=>
    {
        console.log(data)
        return data;

    }).catch((error)=>
    {
        console.log(error)
        return error;
    });
}

