import React,{useEffect,useState} from 'react'
import { Input } from 'antd';
import { AudioOutlined } from '@ant-design/icons';
import Loader from 'react-loader-spinner'
import 'antd/dist/antd.css';
import { Select } from 'antd';
import {
    useDispatch,
     useSelector
    } 
     from 'react-redux'; 
import { instantiateAbort, cleanUpData, SwalAlert } from '../../../helpers/componentHelperFunc';
import { LoadUserTypesAction, ViewUserListAction } from '../../../store/Actions/UserProfileAction';
import {
    useFormFields,
    disableSubmitButton
 }  from '../../../helpers/hooksFormInput'

import { Button } from 'antd';
import { CTooltip } from '@coreui/react';

const { Option } = Select;


const { Search } = Input;

export default function UserFilter(props) 
{
  
    const dispatch =  useDispatch();
     
    const userTypes = useSelector(state => state.userProfile.userTypeResponse);
  
    const abortEffect = instantiateAbort();

    
   const perPage = 5;

    const [fields,setValues,handleFieldChange] = useFormFields({
        input: "",
        option:""
      });  


    useEffect(() => {
         
        dispatch(LoadUserTypesAction());

        return () => {

         cleanUpData(abortEffect)     
        }
    }, [])


   const filterUsers = () =>
   {
       if(fields.input=="" && fields.option=="")
       {
         SwalAlert("Emtpy Fields",`please select a user type or
          enter a text to be filtered by or do both`,"error");
       }else
       {
        props.parentCallBack(fields);
       }
  
   }

   const handleChange = (data) =>
   {
    setValues({
        option:data
    });
   }

    return (
        <div className="container">
        <div className="row">
       
        <div className="col-md-2"></div>

            <div className="col-md-3">
            
            {
                 userTypes == 'loading' || userTypes == '' ?

                 <Loader type="ThreeDots" color="#321fdb" height={20} width={20} />
                 :
                 userTypes.hasOwnProperty('data') && userTypes.success == true ?
                 <Select defaultValue="Select User Type" 
                 
            size="large" 
            // value={fields.option}
            id="option"
            name="option"
            onChange={handleChange}
            style={{width:'100%'}}
            filterOption={(input, option) =>
                option.props.children.toLowerCase().indexOf(input.toLowerCase()) >= 0
              }
            >
                 <Option key={Math.random()} value="" >Select User Type</Option>
                {userTypes.data.map((userType)=>(
              <Option key={Math.random()} value={userType.user_type}>{userType.user_type}</Option>
                ))
                }
      
     
          </Select>
                 :
                 ""

            }
           


            </div>

        <div className="col-md-5">
         
        <Input placeholder="Search For Users Here" 
        value={fields.input}
        id="input"
        name="input"
        onChange={handleFieldChange}
        size="large" style={{width:"55%"}} />
       
  <Button type="primary" onClick={filterUsers} style={{marginLeft:"10px"}} size="large">Search</Button>
        
        <CTooltip
        content={`you can filter both by entering the search input and clicking 
        the search button or selecting a user type and clicking the search button,
         you can also combine both of them to filter user lists`}
        placement={"top"}
        >
          <i className="fa fa-info-circle fa-1x" style={{color:'red',
          marginLeft:'10px',fontSize:'20px'}}></i>
          </CTooltip>
        </div>


    

        </div>
       
     <br/>
   


        </div>
    )
}
