import React,{useState,useEffect} from 'react';
import {
  CBadge,
  CCard,
  CCardBody,
  CCardHeader,
  CCol,
  CTooltip,
  CRow,
  CButton,
  CPagination
} from '@coreui/react';

import 'bootstrap/dist/css/bootstrap.min.css';

import {
  useDispatch,
   useSelector
  } from 'react-redux'
    
  import {
    useFormFields,
    disableSubmitButton
 }  from '../../../helpers/hooksFormInput'
 import {
     cleanUpData,
     instantiateAbort
    } from '../../../helpers/componentHelperFunc'

    import "react-loader-spinner/dist/loader/css/react-spinner-loader.css"

    import Loader from 'react-loader-spinner'
import { 
     ViewUserListAction,
   ActivateOrDeactivateUserAction, 
   EnableOrDisableUserAction 
    } from '../../../store/Actions/UserProfileAction';
import { NavLink } from 'react-router-dom';






export default function UserLists(props)  
{
   
 

  const dispatch =  useDispatch();
     
  const userList = useSelector(state => state.userProfile.userListResponse);

  const activateOrDeactivateUser = useSelector(state=>state.userProfile.userActivationResponse); 
  
  const enableOrDisableUser = useSelector(state=>state.userProfile.userEnableResponse);
   
  const abortEffect = instantiateAbort();

  const perPage = 5;
  
  const [currentPage, setCurrentPage] = useState(1)

   useEffect(() => {
      
        dispatch( ViewUserListAction(perPage,1,props.filterData.input,props.filterData.option)  );
    
     return () => {
       cleanUpData(abortEffect)
     };
   }, [props.filterData])

    useEffect(() => {
      
     dispatch(ViewUserListAction(perPage,1,null,null));
      return () => {
        cleanUpData(abortEffect);
      }
    }, []);


    useEffect(() => {
      
      if(activateOrDeactivateUser!="" && activateOrDeactivateUser!="loading" 
      && activateOrDeactivateUser.success==true)
      {
        if(props.hasOwnProperty("filterData") && props.filterData!=null
     && props.filterData!="")
    {
        dispatch(ViewUserListAction(perPage,currentPage,props.filterData.input,props.filterData.option));
    }else
    {
      dispatch(ViewUserListAction(perPage,currentPage,null,null));
    }
  
      }

      return () => {

      cleanUpData(abortEffect)
      }
    }, [activateOrDeactivateUser])



    useEffect(() => {
      
      if(enableOrDisableUser!="" && enableOrDisableUser!="loading" 
      && enableOrDisableUser.success==true)
      {
        if(props.hasOwnProperty("filterData") && props.filterData!=null
     && props.filterData!="")
    {
        dispatch(ViewUserListAction(perPage,currentPage,props.filterData.input,props.filterData.option));
    }else
    {
      dispatch(ViewUserListAction(perPage,currentPage,null,null));
    }
  
      }

      return () => {

      cleanUpData(abortEffect)
      }
    }, [enableOrDisableUser])


  const loadPageItem = (nextOrPrevPageClicked) =>
  {
    if(props.hasOwnProperty("filterData") && props.filterData!=null
     && props.filterData!="")
    {
      dispatch(ViewUserListAction(perPage,nextOrPrevPageClicked,
        props.filterData.input,props.filterData.option));
  
    setCurrentPage(nextOrPrevPageClicked);
    }else
    {
      dispatch(ViewUserListAction(perPage,nextOrPrevPageClicked,
        props.filterData.input,props.filterData.option));
  
    setCurrentPage(nextOrPrevPageClicked);
    }
    
  } 

  const activeOrDeactivateAccount=(e,id) =>
  {
   dispatch(ActivateOrDeactivateUserAction(id));
  }
   
  const disableOrEnableAccount = (e,id)=>
  {
    dispatch(EnableOrDisableUserAction(id));
  }


  return (
    <>
    <CRow>
        <CCol>
          <CCard>
            <CCardHeader>
             User Lists
            </CCardHeader>
            <CCardBody>
               
               {
                  userList == 'loading' || userList == '' ?

                  <Loader type="ThreeDots" color="#321fdb" height={100} width={100} />
                  :
                  userList.hasOwnProperty('data') && userList.success == true ?
                    <>
<table className="table table-bordered table-striped table-responsive">
    <thead>
      <tr>
        <th>UserName</th>
        <th>Firstnanme</th>
        <th>Latname</th>
        <th>Email</th>
        <th>Activated</th>
        <th>Disabled</th>
        <th>Details</th>
        <th>Account Created</th>
      </tr>
    </thead>
    <tbody>
      {  
        userList.data.data.map((user)=>(
  
          <tr key={user.id}>
   
          <td>{user.username}</td>
          <td>{user.firstname}</td>
          <td>{user.lastname}</td>
      <td>{user.email}</td>
        <td>{
          user.user_activated==1?
      
     <CBadge color="success" style={{cursor:"pointer"}}
          onClick={e=>activeOrDeactivateAccount(e,user.id)}
        >Activated</CBadge>
    
         
          :
     
         <CBadge color="danger" style={{cursor:"pointer"}}
          onClick={e=>activeOrDeactivateAccount(e,user.id)}
        >De-Activated</CBadge>
     
          
          }</td>
          <td>
            {user.user_disabled==1?
            <CBadge color="danger" style={{cursor:"pointer"}}
            onClick={e=>disableOrEnableAccount(e,user.id)}
            >Disabled</CBadge>
            :
            <CBadge color="success" style={{cursor:"pointer"}}
            onClick={e=>disableOrEnableAccount(e,user.id)}
            >Enabled</CBadge>
            }
          </td>
          <td>
            <NavLink to={`/view-user/${user.id}`} >
            <CBadge color="primary" style={{cursor:"pointer"}}>
             <i className="fa fa-eye" aria-hidden="true"></i>
            </CBadge>
            </NavLink>
           
            
            </td>
        <td>{user.created_at}</td>
    </tr>

        ))
      }
     
      
    </tbody>
  </table>

<div style={{marginLeft:10}}>
            <CPagination
            size="sm"
            activePage={currentPage}
            pages={userList.data.total}
            onActivePageChange={loadPageItem}
            pages={userList.data.last_page}
          />
                 
            
            </div>
            </>
            :

            ""
            
           }


            </CCardBody>
            
           

          </CCard>
        </CCol>
      </CRow>
    </>
  );
}
  